.App {
  text-align: center;
}

.date-display {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: end;
      align-items: flex-end;
}
.date-display p {
  margin: 0px;
  padding: 0.5rem;
  font-size: 0.6rem;
}
.date-display #current-date {
  font-size: 1.3rem;
  -ms-flex-item-align: start;
      align-self: flex-start;
}

/* NAVBAR */
.navbar {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row nowrap;
      flex-flow: row nowrap;
  -ms-flex-align: center;
      align-items: center;
  border-bottom: 1px solid #ddd;
  background-color: #ddd;
  overflow: hidden;
  width: 100%;
  max-height: 4rem;
}
.navbar > * {
  margin: 0 1rem;
}
.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

/* MAIN CONTAINER */
.content-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row nowrap;
      flex-flow: row nowrap;
  margin: 2rem 2rem;
}
.content-container > * {
  border: 2px solid #ddd;
}
.content-container > *:first-child {
  -ms-flex-positive: 1;
      flex-grow: 1;
  max-width: 40%;
  margin-right: 1rem;
}
.content-container > *:last-child {
  /*background-color: grey;*/
  -ms-flex-positive: 3;
      flex-grow: 3;
  max-width: 60%;
}
.main-view-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}
.main-view-container > * {
  margin: 1rem;
}
/* I thought this fixed my button icon problems
it appears to have done the opposite
i {
  width: 1rem;
  height: 1rem;
  margin: 0.3rem 0.3rem;
}
*/
.stock-price-icon {
  width: auto;
  height: auto;
  margin: 0.3rem 0.3rem;
  max-width: 1rem;
  max-height: 1rem;
}
.stock-price-raised {
  color: green;
  text-align: center;
}
.stock-price-no-change {
  color: inherit;
  text-align: center;
}
.stock-price-lowered {
  color: red;
  text-align: center;
}

